@import 'partials/detail';
@import 'partials/product';
@import 'partials/cart';
@import 'partials/parallax';
@import 'partials/admin';

$project-grey: #BDBBBB;
$project-pink: #F77EB5;

body {
  position: relative;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 18px;
  nav {
    background: $project-grey; }
  nav .navLinks {
    a {
      color: Black; } } }

hr {
  clear: both;
  margin: 0;
  border: none; }

body > main {
  nav {
    position: absolute;
    height: 70px; } }

section {
  margin-top: 20px;
  margin-bottom: 20px; }

h2 {
  text-align: center;
  margin-bottom: 30px;
  font-family: 'Lobster', cursive;
  font-size: 34px; }

h3 {
  text-align: center;
  margin-bottom: 30px;
  font-family: 'Lobster', cursive;
  font-size: 24px; }

a {
  text-decoration: none; }

button {
  outline: none; }

footer {
  position: relative;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $project-grey;
  img {
    transform: scale(0.5); }
  li {
    height: auto;
    list-style: none;
    text-align: center;
    font-size: 14px; }
  a {
    padding: 15px;
    color: black;
    &:hover {
      text-decoration: none;
      color: black; } } }

#sweetNav {
  position: fixed;
  z-index: 3; }

.navLinks {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background: transparent;
  z-index: 1;
  .logo {
    padding: 15px 10px 10px 15px;
    height: 130px;
    display: inline-block;
    border-bottom-right-radius: 66px;
    .logoImg {
      display: inline-block;
      width: 170px;
      height: 105px;
      background: url(/../img/logoNav.png) bottom center no-repeat; }
    .logoImgBlack {
      background: url(/../img/logoBlack.png) bottom center no-repeat;
      display: inline-block;
      width: 140px;
      height: 80px;
      background-size: contain; } }


  ul {
    padding: 15px;
    list-style: none;
    display: inline-block;
    margin: 0; } }

.nav-colored {
  background-color: rgba(0, 0, 0, 0.78); }

.deskNav {
  a {
    color: white;
    font-family: 'Lobster', cursive;
    font-size: 24px;
    background: transparent;
    border: none;
    &:hover {
      color: $project-grey; } } }

.list-inline.movNav {
  padding-top: 24px; }

.clMenu {
  width: 63px;
  text-align: center;
  padding-top: 6px;
  span {
    transform: scale(1.3);
    color: black; } }

.menu {
  color: white;
  font-family: 'Lobster', cursive;
  font-size: 24px;
  background: transparent;
  border: none;
  &:hover {
    color: $project-grey; } }

.menuOption {
  height: auto;
  padding: 5px;
  font-family: 'Lobster', cursive;
  font-size: 40px;
  margin: 0;
  width: 100%;
  &:hover {
    color: white;
    background-color: grey;
    a {
      color: white; } }
  a {
    color: black; }
  & a:hover {
    text-decoration: none; } }


.slideup, .slidedown {
  max-height: 0;
  overflow-y: hidden;
  -webkit-transition: max-height 0.5s ease-in-out;
  -moz-transition: max-height 0.5s ease-in-out;
  -o-transition: max-height 0.5s ease-in-out;
  transition: max-height 0.5s ease-in-out; }

.slidedown {
  max-height: 700px ; }

.cartIcon {
  display: flex;
  align-items: center;
  margin-right: 20px;
  &:hover {
    .glyphicon-shopping-cart {
      color: $project-grey; }
    .numberCircle {
      color: $project-grey;
      border: 2px solid $project-grey; } } }

.glyphicon-shopping-cart {
  color: white;
  transform: scale(1.2); }

.numberCircle {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: transparent;
  border: 2px solid white;
  color: white;
  text-align: center;
  display: inline-block;
  font-size: 12px; }


.intro {
  background-size: cover;
  margin: 0;
  width: 100%;
  height: 500px;
  position: relative;
  div {
    width: 100%;
    position: absolute;
    top: 200px;
    p {
      font-family: 'Marck Script', cursive;
      font-size: 30px;
      margin: auto;
      text-align: center;
      color: white;
      width: 70%; } } }

.introNav {
  position: absolute;
  bottom: 5px;
  width: 100%;
  text-align: center; }

.introBtns {
  padding: 10px;
  margin: 15px;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
  background: transparent;
  font-size: 18px;
  &:hover {
    text-decoration: none;
    color: $project-grey;
    border: 2px solid $project-grey; } }

.introPar {
  font-family: 'Marck Script', cursive;
  font-size: 24px;
  padding: 40px;
  text-align: center; }

.hide {
  display: 'none'; }

.imgContainer {
  img {
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
    margin: auto;
    padding: 10px; } }

.sectionBtns {
    text-align: center;
    height: 45px;
    margin-top: 20px;
    margin-bottom: 20px;
    a {
      display: inline;
      padding: 10px;
      margin: 15px;
      border: 2px solid black;
      border-radius: 5px;
      color: black;
      background: transparent;
      font-size: 18px;
      &:hover {
        text-decoration: none;
        color: $project-pink;
        border: 2px solid $project-pink; } } }

.social {
  position: absolute;
  top: 15px;
  right: 5%;

  li {
    font-size: 33px; }
  i {
    color: black; } }


/************** Repsonsive elements *********************/

// x-small devices

@media (max-width: 575px) {

  .navLinks {
    .logo {
      height: 100px;
      .logoImg {
        width: 113px;
        height: 70px;
        background-size: contain; } } }

  .intro {
    div {
      p {
        font-size: 22px; } } }

  ul.list-inline.deskNav {
    display: none; }

  .introNav {
    display: none; } }


// small devices

@media (min-width: 576px) and (max-width: 768px) {
  .navLinks {
    .logo {
      height: 100px;
      .logoImg {
        width: 113px;
        height: 70px;
        background-size: contain; } } }

  .intro {
    div {
      p {
        font-size: 26px; } } }
  ul.list-inline.deskNav {
    display: none; } }


// medium devices

@media (min-width: 769px) and (max-width: 992px) {
  .modal-dialog {
    width: 650px; }

  .menu {
    display: none; } }


// large devices

@media (min-width: 993px) {

  .menu {
    display: none; }

  .introNav {
    bottom: 40px; } }




