$project-grey: #BDBBBB;
$project-pink: #F77EB5;

table {
  text-align: center;
  width: 100%; }

thead {
  border-bottom: 1px solid $project-grey; }

th, td {
  text-align: center;
  vertical-align: center; }

td {
  height: 100px; }

td.price {
    border: none; }

.cart {
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top: -250px;
  margin-bottom: 50px;
  z-index: 1;
  position: relative;
  background-color: white;
  border: 1px solid $project-pink; }

.cartArticles {
  padding: 30px;
  border: 1px solid $project-grey;
  article div {
    padding: 0;
    border-bottom: 1px solid $project-grey; } }

.infoCart {
  margin-top: 30px; }

.continue {
  span {
    color: black; } }

.checkout {
  width: 100%; }

.btn.remove {
  background-color: transparent;
  color: red;
  border: 1px solid red;
  outline: none; }

.totals {
  background-color: $project-grey;
  border-radius: 4px;
  padding-left: 0;
  .col-xs-7 {
    p {
      text-align: right; } } }










