$project-grey: #BDBBBB;
$project-pink: #F77EB5;

.proArticles {
  margin: 30px; }

.itemName {
  font-family: 'Lobster', cursive;
  font-size: 24px; }

.selector {
  padding: 5px 0px 10px 5px;
  border: 1px solid #ddd;
  border-right: none;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  p {
    text-align: center; } }

.price {
  padding: 5px 0px 10px 5px;
  border: 1px solid #ddd;
  border-left: none;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  p {
    text-align: center; } }


.itemReviews {
  text-align: center; }

.addProduct, .continue {
  color: black;
  background: #f0c14b;
  border-color: #a88734 #9c7e31 #846a29;
  .glyphicon-plus-sign {
    transform: scale(0.8); }
  .glyphicon-shopping-cart {
    color: black; }
  &:hover {
    background: #C29F31;
    color: white;
    .glyphicon-shopping-cart {
      color: white; } } }




